.gallery-hero {
  padding-top: 7rem;
  margin-bottom: 20rem;
  position: relative;
  @include mobile {
    padding-top: 5.6rem;
    margin-bottom: 16.8rem;
  }
  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    left: -93rem;
    top: -38.5rem;
    width: 146rem;
    aspect-ratio: 1 / 1;
    background-image: url("./../img/lines/main1.svg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;

    transform: rotate(-49deg);

    @include mobile {
      width: 75rem;
      left: -17rem;
      top: 6.7rem;
      transform: rotate(-165deg);
    }
  }
  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    right: -101.5rem;
    bottom: -12rem;
    width: 146rem;
    aspect-ratio: 1 / 1;
    background-image: url("./../img/lines/main1.svg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;

    transform: rotate(135deg);
    @include mobile {
      display: none;
    }
  }
  &__title {
    padding: 4.4rem;
    padding-bottom: 5.3rem;
    margin-bottom: 4.1rem;
    background: #fff;
    mask-image: url("./../img/gallery/header-mask.svg");
    mask-size: 100% 100%;
    mask-repeat: no-repeat;
    mask-position: center;

    @include mobile {
      mask-image: url("./../img/gallery/header-mask-mob.svg");
      margin-bottom: 2rem;
      padding: 4rem;
    }
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
    @include mobile {
      display: flex;
      flex-direction: column;
      gap: 3rem;
    }
  }

  &__item {
    &:last-child {
      grid-column: 2 span;
    }
  }
}

.gallery-item {
  cursor: pointer;
  &__img {
    height: 54.6rem;
    mask-size: 100% 100%;
    mask-repeat: no-repeat;
    mask-position: center;
    @include mobile {
      height: 32rem;
    }
    img {
      width: 100%;
      height: 100%;
    }
  }

  &__info {
    display: flex;
    justify-content: space-between;

    mask-size: auto;
    mask-repeat: no-repeat;
    mask-position: center;
    background-color: #fff;

    font-size: 3.2rem;
    line-height: 120%;
    color: var(--black);

    padding: 4rem 5.2rem;
    padding-right: 5.1rem;

    @include mobile {
      font-size: 2rem;
      padding: 2.9rem 4.2rem 3.1rem;
    }
  }
  &__name {
    font-weight: 500;
  }
  &:nth-child(3n + 1) {
    .gallery-item__img {
      mask-image: url("./../img/gallery/g-mask-1.svg");
    }
    .gallery-item__info {
      mask-image: url("./../img/gallery/info-mask-1.svg");
    }
  }
  &:nth-child(3n + 2) {
    .gallery-item__img {
      mask-image: url("./../img/gallery/g-mask-2.svg");
    }
    .gallery-item__info {
      mask-image: url("./../img/gallery/info-mask-2.svg");
    }
  }
  &:last-child {
    .gallery-item__img {
      border-radius: 3rem;
      overflow: hidden;
      margin-bottom: 2.4rem;
      @include mobile {
        margin-bottom: 0;
      }
    }
    .gallery-item__info {
      border-radius: 3rem;
      padding-bottom: 4.9rem;
      padding-top: 4.6rem;

      @include mobile {
        padding: 2.9rem 4.2rem 3.1rem;
      }
    }
  }

  @include mobile {
    &:nth-child(odd) {
      .gallery-item__img {
        mask-image: url("./../img/gallery/g-mask-1-mob.svg");
      }
      .gallery-item__info {
        mask-image: url("./../img/gallery/info-mask-1-mob.svg");
      }
    }
    &:nth-child(even) {
      .gallery-item__img {
        mask-image: url("./../img/gallery/g-mask-2-mob.svg");
      }
      .gallery-item__info {
        mask-image: url("./../img/gallery/info-mask-2-mob.svg");
      }
    }
  }
}
